:root{box-sizing:border-box;font-size:calc(1em + 1vw);line-height:1.2}*,::before,::after{box-sizing:inherit}body *+*{margin-top:.5em}body{margin:0;padding:0;background-color:#575fcf;color:#fff}.timer{position:absolute;display:grid;grid-template-rows:1fr;grid-template-columns:repeat(3, auto);top:50%;left:50%;transform:translateX(-50%) translateY(-50%)}.length-control{grid-column:1 / 2;grid-row:span 1;display:grid;grid-template-columns:repeat(1, auto);grid-template-rows:repeat(2, auto)}.control-title{text-align:center;grid-column:span 1;grid-row:1 / 2}.control-container{grid-row:2 / 3;grid-column:span 1;display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:space-between}.control-btn{background:none;color:inherit;border:none;padding:0;font:inherit;font-size:1.1rem;cursor:pointer;outline:inherit}.control-buttons{grid-row:1 / 3;grid-column:2 / 3;margin:1em 3em;font-size:1.2rem;display:flex;flex-direction:column;justify-content:space-between}.display{grid-row:1 / 3;grid-column:3 / 4;font-size:1.5rem;font-weight:bold;display:flex;flex-direction:column;justify-content:center;align-items:center;width:4em;margin:0;padding:0}
